.nav-tabs {
  margin-left: 6%;
  border-bottom: 1px solid #999999;
}
.nav-tabs > li > a {
  border: none;
  text-align: center;
}
.nav-tabs > li > a,
.nav-tabs > li > a:focus,
.nav-tabs > li > a:hover {
  border: none;
  background: transparent;
}
.nav-tabs > li > a .image-how,
.nav-tabs > li > a:focus .image-how,
.nav-tabs > li > a:hover .image-how {
  -webkit-filter: grayscale(0%);
  filter: grayscale(0%);
}
.nav-tabs > li.active .image-how {
  -webkit-filter: grayscale(0%);
  filter: grayscale(0%);
}
.nav-tabs li .active a .blocks-how .image-how {
  -webkit-filter: grayscale(0%) !important;
  filter: grayscale(0%) !important;
}
.nav-tabs > li.active > a > .blocks-how > .img-how {
  -webkit-filter: grayscale(0%) !important;
  filter: grayscale(0%) !important;
}
.nav-tabs > li.active > a,
.nav-tabs > li.active > a:focus,
.nav-tabs > li.active > a:hover,
.nav-tabs > li.active > a:active {
  border: none;
  background: transparent;
}
.nav-tabs > li.active > a .image-how,
.nav-tabs > li.active > a:focus .image-how,
.nav-tabs > li.active > a:hover .image-how,
.nav-tabs > li.active > a:active .image-how {
  -webkit-filter: grayscale(0%) !important;
  filter: grayscale(0%) !important;
}
.nav-tabs > li > a > .blocks-how > .img-active {
  display: none;
}
.nav-tabs > li.active > a > .blocks-how > .img-active {
  position: absolute;
  left: 50%;
  margin-left: -8px;
  bottom: 0;
  display: block;
}
@media all and (min-width: 320px) and (max-width: 780px) {
  .nav-tabs > li.active > a > .blocks-how > .img-active {
    display: none;
  }
}
.nav-tabs > li > a > .blocks-how {
  text-align: center;
  height: 130px;
  width: 100px;
}
.nav-tabs > li > a > .blocks-how:active .image-how {
  -webkit-filter: grayscale(0%);
  filter: grayscale(0%);
}
.nav-tabs > li > a > .blocks-how > .image-how {
  position: absolute;
  bottom: 75px;
  left: 34px;
  -webkit-filter: grayscale(100%);
}
.nav-tabs > li > a > .blocks-how > p {
  /*font-family: RobotoCondensed;*/
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center;
  margin-top: 20px;
  position: absolute;
  bottom: 10px;
  width: 100%;
  left: 0;
}
.nav-tabs > li::after {
  content: " ";
  background: url(img/str.png) no-repeat;
  width: 53px;
  height: 46px;
  display: inline-block;
  position: absolute;
  top: 60px;
  right: -50px;
}
.nav-tabs > li:last-child:after {
  display: none;
}
