.c100:hover {
  cursor: default;
}
.c100:hover > span {
  width: 90px;
  line-height: 90px;
  font-size: 24px;
  color: #fff;
}
.c100:hover:after {
  top: 0.04em;
  left: 0.04em;
  width: 0.92em;
  height: 0.92em;
}
.c100.dark {
  background-color: #777;
}
.c100.dark .bar,
.c100.dark .fill {
  border-color: #c6ff00 !important;
}
.c100.dark > span {
  color: #777;
}
.c100.dark:after {
  background-color: #666;
}
.c100.dark:hover > span {
  color: #c6ff00;
}
.c100.green .bar,
